button{
  background-color: #06a700;
  border-radius: 10px;
  border: none;
  padding: 15px 25px;
  float: right;
  color: white;
  transition: box-shadow 0.4s, background-color 0.4s;
  cursor: pointer;
}

button:hover{
  background-color: #07c500;
  border-radius: 10px;
  border: none;
  padding: 15px 25px;
  float: right;
  color: white;
  box-shadow: 0 0 15px #1f1e1eea;
  transition: box-shadow 0.3s, background-color 0.3s;
}

.grid-container {
  display: grid;
  grid-template-columns: auto auto auto auto auto auto;
  border-radius: 10px;
  margin-top: 15px;
  padding: 10px;
  justify-items: center;
}


.imagem{
  border: 2px solid rgb(66, 66, 66);
  border-radius: 10px;
  box-shadow: 0 0 15px rgb(99, 99, 99);
  background: #f93;
  background: -webkit-gradient(radial, from(#ff0), to(#f93));
  background: -webkit-radial-gradient(center, #ff0, #f93);
}

